import React from 'react'
import Grid from '../Components/Grids/Grid'
import CategoryList from '../Components/Categories/CategoryList';
import { connect } from 'react-redux'
import { setCategory, setCatContent } from '../Actions/content';
import Axios from 'axios';
import CategorySingle from '../Components/Categories/CategorySingle';

class Category extends React.Component{
  state = {
    content: false,
    category: false,
    filter: ''
  }

  componentDidMount() {
    Axios.get(`/api/category`).then((res) => {
      // console.log(res.data)
      this.props.setCategory(res.data);
      this.setState({
        category: true,
        filter: res.data[0]._id
      })
      this.filterComponent();
    }).catch((e) => {
      this.setState({
        category: false
      })
      console.log('error found in ./api/content/', e);
    })
  }

  render(){
    const props = this.props;
    return (
      <div className="content">
      <div className="categories">
        {this.state.category ? 
          <CategoryList category={this.props.category} filter={this.filterCategory}/>
        : 'Loading'}
        <div className="categories-types">
          <CategorySingle firstFilter={this.state.filter} cat_id={props.location.search.replace('?id=', '')}/>
          <div className="recent-videos">
            <h5><img src="/images/recent.png" alt="" />Most-View-videos</h5>
            {/* <div className="grids">
              <Grid/>
              <Grid/>
              <Grid/>
            </div> */}
          </div>
        </div>
      </div>
      <div className="clear"> </div>
      </div>
    )
  }
}
const mapStateToProps = (state) => ({
  allContent: state.content.all,
  category: state.content.category,
  cat_content: state.content.cat_content
});

const mapDispatchToProps = (dispatch) => ({
  setCategory: (data) => dispatch(setCategory(data)),
  setCatContent: (data) => dispatch(setCatContent(data))
})

export default connect(mapStateToProps, mapDispatchToProps)(Category)
